body{
    /*background-color: #e6b54a;*/
    margin: 0px;
    padding: 0px;


}
 .spinner{
     z-index: 100;
     position:absolute;
     background: rgba(52, 38, 0, 0.73);
     width: 100%;
     height: 100%;

     transition: 5s;
 }


.cup{
    z-index: 100000;
    height: 140px;
    width: 180px;
    /*border: 6px solid #e6b54a;*/
    position: absolute;
    top: 40%;

    left: 40%;
    border-radius: 0px 0px 70px 70px;
    background-color: rgba(243, 62, 62, 0);
    background-image: url(./coffee5.png);
    box-shadow: 0px 0px 0px 6px #797373;
    background-repeat: repeat-x;
    background-position: 0px 140px;
    animation: fill 5s infinite;
}

.handle{
    height: 70px;
    width: 40px;
    /*background-color: transparent;*/

    border: 6px solid #797373;
    color: white;
    /*border-width: 6px;*/
    /*border: 5px solid;*/
    border-radius: 0px 25px 80px 0px;
    /*border-image: linear-gradient(45deg, #483f48, #b0aba6) 1;*/
    position: relative;
    left: 186px;
    top: 2px;

}


@keyframes fill{
    0%{
        background-position: 0px 140px;
        background-color: rgba(243, 171, 62, 0.27);
    }
    20%
    {
        background-color: rgba(114, 121, 208, 0.42);
        background-position: -450px 100px;
    }
    40%{
        background-color: rgba(185, 114, 208, 0.42);
        background-position: -900px 50px;
    }
    80%{
        background-color: rgba(255, 137, 141, 0.21);
        background-position: -1350px -40px;
    }
    100%{
        background-color: rgba(208, 200, 114, 0.42);
        background-position: 0px 140px;;
    }
}

@media (max-width: 490px) {
    .cup{
        left: 25%;
    }
}